import { Component, OnInit } from '@angular/core';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { DashboardService } from '../../../service/dashboard.service';

import { AppointmentComponent } from './../appointment/appointment.component'
@Component({
  selector: 'app-appointment-create',
  templateUrl: './appointment-create.component.html',
  styleUrls: ['./appointment-create.component.css']
})
export class AppointmentCreateComponent extends AppointmentComponent implements OnInit {
  //
  headerTitle: string;
  //
  startDateValue: Date = new Date;
  //
  startTimeCode: string = "00";
  //
  endDateValue: Date = new Date;
  //
  endTimeCode: string = "00";
  // 
  timeJson: any = [];
  //
  allDay: number = 0;
  //
  personCode: number = 3;
  //
  siteCode: string = ""

  //
  textDetail: string = "";

  //
	checkUser: any = [];

  constructor(protected Dash: DashboardService) {
    super(Dash);
    this.getTypeList();
    this.getRecipientList();
  }

  ngOnInit() {
    this.changeTimeJson();
  }

  //
  createNewApp() {
    let params = {
      // StudentID:,
      // Start: ,
      // End:,
      Title:this.headerTitle,
      Description:this.textDetail,
      Location:this.siteCode,
      // StatusID:,
      TypeID:this.personCode,
      IsAllDay:this.allDay,
      // IsComplete:,
      // GroupNotifiedUserTypes:,
    };
    this.Dash.POST(this.Dash.config.createApp,params).then(result => {
      if(result) alert("!");
      console.log(result);
    });
  }

  //
  startDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.startDateValue = event.value;
  }

  //
  endDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.endDateValue = event.value;
  }

  //json
  changeTimeJson() {
    this.timeJson = [
      { name: "0:00", value: "00" },
      { name: "1:00", value: "01" },
      { name: "2:00", value: "02" },
      { name: "3:00", value: "03" },
      { name: "4:00", value: "04" },
      { name: "5:00", value: "05" },
      { name: "6:00", value: "06" },
      { name: "7:00", value: "07" },
      { name: "8:00", value: "08" },
      { name: "9:00", value: "09" },
      { name: "10:00", value: "10" },
      { name: "11:00", value: "11" },
    ];
  }
  
  //
  backPage() {
    history.back();
  }
}
