import { Component, ChangeDetectionStrategy, OnInit,ChangeDetectorRef  } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../service/auth.service';
// import { enableProdMode } from '@angular/core';
// import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// import { Router, ActivatedRoute, ParamMap, Event, RoutesRecognized, RouterOutlet } from '@angular/router';

@Component({
  selector: 'app-dashboard-layout',
  templateUrl: '../customer.component.html',
  styleUrls: ['../customer.component.css'],
  providers: []
})
export class DashboardComponent implements OnInit {
  show_header = true;
  show_sidebar = true;
  title = 'GuidedPath Customer';
  
  constructor(public router: Router, private cd: ChangeDetectorRef,public auth: AuthService) {
    this.auth.getUserinfo_flag.subscribe(res => {
      if(!res.login_status){
        this.router.navigateByUrl("/account/logon");
      }
    });
  }

  ngOnInit() {
    //this.router.navigate(['/account/logon']);
    //this.router.navigate(['/Home/AdvisorHome']);
    //this.router.navigate(['dashboard/index']);
  }
}