import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';

@Component({
  selector: 'app-assignment',
  templateUrl: './assignment.component.html',
  styleUrls: ['./assignment.component.css']
})
export class AssignmentComponent implements OnInit {

  //
  assignment_list: any = [];
  //
  recipientsList: any = {
    student: [],
    advisor: [],
    connection: []
  };

  //
  checkRecipients: any = {
    student: [],
    advisor: [],
    connection: []
  };
  //
  typeList: any = [];
  //
  showNoneMessage: boolean = true;

  //
  assignedFlag: boolean = true;
  //
  draftFlag: boolean = true;
  //
  canceledFlag: boolean = true;

  constructor(protected Dash: DashboardService) { }

  ngOnInit() {
    this.getAssignmentsList();
    if (location.pathname != "/assignment") {
      this.showNoneMessage = false;
    }
  }

  //
  getAssignmentsList() {
    let params = {
      assigned: this.assignedFlag ? 1 : 0,
      draft: this.draftFlag ? 1 : 0,
      canceled: this.canceledFlag ? 1 : 0
    };
    this.Dash.GET(this.Dash.config.getAssList, params).then(result => {
      this.assignment_list = result;
      console.log(result);
    });
  }

  //
  getAssignmentRecipients() {
    this.Dash.GET(this.Dash.config.getAssRecipients).then(result => {
      this.recipientsList = result;
      console.log(result);
    });
  }



  //
  getAssignmentTypeList() {
    this.Dash.GET(this.Dash.config.getAssTypeList).then(result => {
      this.typeList = result;
    });
  }

  checkboxChange(event, type) {
    if (type == "assigned") {
      this.assignedFlag = event.checked;
    }
    if (type == "draft") {
      this.draftFlag = event.checked;
    }
    if (type == "canceled") {
      this.canceledFlag = event.checked;
    }
    this.getAssignmentsList();
  }

}
