import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';

@Component({
  selector: 'invoice-select-bill',
  templateUrl: './invoice-select-bill.component.html',
  styleUrls: ['./invoice-select-bill.component.css']
})
export class InvoiceSelectBillComponent implements OnInit {

  //
  checkBill: number = 0;

  constructor(public dialogRef: MatDialogRef<InvoiceSelectBillComponent>,
    public dash: DashboardService,
    protected auth: AuthService,
    @Inject(MAT_DIALOG_DATA) public billList: any) {
    this.checkBill = billList[0];
  }


  ngOnInit() { }

  closeEvent(check): void {
    this.dialogRef.close(check);
  }

}