import { Component, OnInit, Inject, forwardRef } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';

import { SaveDraftService } from './../../common/service/save-draft.service';
import { MessageComponent } from './../message/message.component'
@Component({
	selector: 'app-message-view',
	templateUrl: './message-view.component.html',
	styleUrls: ['./message-view.component.css']
})
export class MessageViewComponent extends MessageComponent implements OnInit {

	// ID
	senderId: number = null;

	//  inbox:1 || draft:0
	view_type: number | string;

	view_id: string;

	view_message: any = {
		param: {},
		msg: []
	};

	//
	showReplyView: boolean = true;

	//
	showAllText: boolean = false;

	//
	subjectText: string = "";

	//
	textDetail: string = "";

	//
	checkUser: any = [];

	constructor(protected Dash: DashboardService,
		protected location: Location,
		protected auth: AuthService,
		protected router: Router,
		protected route: ActivatedRoute,
		protected saveDraft: SaveDraftService,
		@Inject(forwardRef(() => MessageComponent)) public parent: MessageComponent) {
		super(Dash, location, auth, router);
	}

	ngOnInit() {
		this.getRecipients();
		this.getSessionInfo();
		this.route.params.subscribe(params => {
			this.view_id = decodeURI(this.route.snapshot.paramMap.get('id'));
			this.view_type = decodeURI(this.route.snapshot.paramMap.get('type'));
			this.showReplyView = true;
			this.getDetail();
		});
	}

	//
	getDetail() {
		this.Dash.GET(this.Dash.config.getMsgDetail, {
			ConversationID: this.view_id
		}, true).then(result => {
			this.view_message = result;
			console.log(result);
			if (this.view_type == 0) {
				this.checkRecipients = result.param.Recipients;
				this.subjectText = result.param.Subject;
				this.textDetail = result.msg[0].MessageText;
			}
		});
	}

	// type 0=>Email 1=>Text 2=>all
	replyMessage(type) {
		let option: any = {
			ConversationID: this.view_message.msg[0].ConversationID,
			Sender: this.senderId,
			RecipientList: this.checkUser,
			text: this.textDetail,
			is_text: type
		};
		this.Dash.POST(this.Dash.config.replyMsg, option, true).then(result => {
			if (result) alert("!");
		});
	}

	//
	sendDraftMessage() {
		let option: any = {
			ConversationID: this.view_message.msg[0].ConversationID,
			MessageID: this.view_message.msg[0].ID,
			StudentID: this.studentId,
			Sender: this.senderId,
			RecipientList: this.checkUser,
			subject: this.subjectText,
			text: this.textDetail
		};
		this.Dash.POST(this.Dash.config.sendMsgDraft, option, true).then(result => {
			this.router.navigateByUrl(`/message/view/1/${this.view_id}`);
			this.parent.getConversations();
		});
	}

	//
	removeDraft() {
		this.Dash.POST(this.Dash.config.removeDraftMsg, {
			ConversationID: this.view_message.msg[0].ConversationID
		}, true).then(result => {
			if (result == true) alert("");
		});
	}

	//
	saveDraftData() {
		if (this.view_type == 1 || this.view_message.msg.length == 0) return;

		this.saveDraft.saveDraft({
			ConversationID: this.view_message.msg[0].ConversationID,
			MessageID: this.view_message.msg[0].ID,
			Sender: this.senderId,
			RecipientList: this.checkUser,
			subject: this.subjectText,
			text: this.textDetail,
		});
	}
}