import {
  Component,
  OnInit,
} from '@angular/core';
import { Subject } from 'rxjs';
import {
  CalendarEvent,
  CalendarView
} from 'angular-calendar';
import {
  startOfDay,
  subDays,
  addDays,
  isSameDay,
  isSameMonth
} from 'date-fns';

import { MatDialog } from '@angular/material';

import { CalendarViewComponent } from './../calendar-view/calendar-view.component';


@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.css']
})

export class CalendarComponent implements OnInit {
  //
  viewDate: Date = new Date();
  refresh: Subject<any> = new Subject();
  //
  activeDayIsOpen: boolean = false;
  //
  isshowagenda: boolean = false;
  //
  isShowOpenDate: boolean = false;
  
  constructor(public dialog: MatDialog) { }

  ngOnInit() {

  }

  view: CalendarView = CalendarView.Month;

  CalendarView = CalendarView;

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  events: CalendarEvent[] = [
    {
      start: subDays(startOfDay(new Date()), 1),
      end: addDays(new Date(), 1),
      title: 'this is Appointments!',
      color: {
        primary: '#609cfe',
        secondary: '#e0eafa'
      },
      allDay: true,
      resizable: {
        beforeStart: true,
        afterEnd: true
      },
      draggable: false
    },
    {
      start: subDays(startOfDay(new Date()), 1),
      end: addDays(new Date(), 1),
      title: 'this is Assignment!',
      color: {
        primary: '#92c34a',
        secondary: '#e3eedb'
      },
      allDay: true,
      resizable: {
        beforeStart: true,
        afterEnd: true
      },
      draggable: false
    }
  ];

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      this.viewDate = date;
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
    }
  }

  openDialog(action: string, event: CalendarEvent): void {
    const dialogRef = this.dialog.open(CalendarViewComponent, {
      height: '470px',
      width: '600px', 
      data: {
        // animal: 'panda' 
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

}