import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';

@Component({
  selector: 'app-user-select',
  templateUrl: './user-select.component.html',
  styleUrls: ['./user-select.component.css']
})
export class UserSelectComponent implements OnInit, OnChanges {
  // line:  col:
  @Input() type:string = "line";
  //
  @Input() iconName:string = "account_circle";
  //
  @Input() title:string = "Recipients";
  //
  @Input() data:any = {
    student: [],
    advisor: [],
    connection: []
  };
  //
  @Input() checkData: any = {
    student: [],
    connection: [],
    advisor: []
  };

  @Output() getCheckData = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {
  
  }

  ngOnChanges() {
    this.getCheckRecipients();
  }

  //
  filterRecipients(list = [], id): boolean {
    let flag: boolean = false;
    for (let item of list) {
      if (item.ID == id) {
        flag = true;
        break;
      }
    }
    return flag;
  }


  //
  addRecipients(type, item) {
    if (type == 0 && !this.filterRecipients(this.checkData.student, item.ID)) {
      this.checkData.student.push(item);
      this.getCheckRecipients();
      return;
    }
    if (type == 1 && !this.filterRecipients(this.checkData.connection, item.ID)) {
      this.checkData.connection.push(item);
      this.getCheckRecipients();
      return;
    }
    if (type == 2 && !this.filterRecipients(this.checkData.advisor, item.ID)) {
      this.checkData.advisor.push(item);
      this.getCheckRecipients();
    }
  }

  
  //
  delectRecipients(type, index) {
    if (type == 0) {
      this.checkData.student.splice(index, 1);
      this.getCheckRecipients();
      return;
    }
    if (type == 1) {
      this.checkData.connection.splice(index, 1);
      this.getCheckRecipients();
      return;
    }
    if (type == 2){
      this.checkData.advisor.splice(index, 1);
      this.getCheckRecipients();
    } 
  }

  //
  getCheckRecipients(){
    let checkRecipients:any = []; 
    for(let item of this.checkData.student) checkRecipients.push(item.ID);
    for(let item of this.checkData.connection) checkRecipients.push(item.ID);
    for(let item of this.checkData.advisor) checkRecipients.push(item.ID);
    this.getCheckData.emit(checkRecipients);
  }
}
