import { Component, OnInit } from '@angular/core';

import { SelectionModel } from '@angular/cdk/collections';
import { MatTableDataSource } from '@angular/material';
import {FormControl} from '@angular/forms';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { CalendarEvent } from 'angular-calendar';



export interface AppointmentsElement {
  Date: string;
  Time: string;
  Event:string;
}
export interface ToDoElement {
  color: string;
  dueDate: string;
  priority: string;
  title:string;
}
export interface StudentElement{
  imgsrc:string;
  name:string;
  appyear:string;
  lastloggedin :string;
  status:string;
  primaryadvisor:string;
  appointment:string;
  studentcategory:string;
}
export interface StudentTasksElement{
  color:string;
  dueDate:string;
  student:string;
  assignment:string;
}
const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};
@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
  showDayOrAgenda:boolean;
  DueDateData = new FormControl();
  DueDateList: any[] = [
    {name:'Overdue',color:'darkorange'},
    {name:'Today',color:'orange'},
    {name:'Next 7 Days',color:'yellow'},
    {name:'Next 30 days',color:'green'}
  ];
  constructor(public auth: AuthService, public router:Router) { 
    //student
    if(this.auth.userinfo.login_user.userRole=='advisor' && this.auth.userinfo.login_user.viewRole=='student'){
      this.unselect_student();
    }
  
  }

  ngOnInit() {
    this.showDayOrAgenda = true;
    this.initToDoList();
    this.initAppointmentsList();
    this.initStudent();
    this.initToDoList1();
  }
  
  unselect_student(){
    this.auth.api_unselect_student().then(res => {
      this.auth.get_userinfo();
    });

  }
  //Appointments Change Agenda or Day
  changeDayOrAgenda(event):void{
    event.value==='Day' ? this.showDayOrAgenda = false :this.showDayOrAgenda =true;
  }

  viewDate: Date = new Date();

  events: CalendarEvent[] = [
    {
      title: 'An all day event',
      color: colors.yellow,
      start: new Date(),
      allDay: true
    },
    {
      title: 'A non all day event',
      color: colors.blue,
      start: new Date()
    }
  ];

  //AppointmentsList
  appointmentsColumns: string[] = ['Date', 'Time', 'Event'];
  appointmentsListData:any=null;

  initAppointmentsList(){
  let appointments: AppointmentsElement[] = [
    {Date: '11/20/2018', Time: '2:00 PM - 3:00 PM', Event: "Chad International - Sep 2"},
    {Date: '11/20/2018', Time: '2:00 PM - 3:00 PM', Event: "Chad International - Sep 2"},
    {Date: '11/20/2018', Time: '2:00 PM - 3:00 PM', Event: "Chad International - Sep 2"}
  ];
  this.appointmentsListData = appointments;

  }
  //To-Do list
  toDoColumns: string[] = ['select', 'color', 'dueDate', 'priority','title'];
  taskSelection = new SelectionModel<ToDoElement>(true, []);
  toDoListData:any=null;
  initToDoList(){
    let tasks: ToDoElement[] = [
      {color: '#F8A402', dueDate: '11/12/2018', priority: 'Hight' ,title:'Title1'},
      {color: '#FFF04D', dueDate: '11/21/2018', priority: 'Medium',title:'Title1'},
      {color: '#FFF04D', dueDate: '11/24/2018', priority: 'Medium',title:'Title1'},
    ];
    this.toDoListData = new MatTableDataSource<ToDoElement>(tasks);

  }
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.taskSelection.selected.length;
    const numRows = this.toDoListData.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.taskSelection.clear() :
        this.toDoListData.data.forEach(row => this.taskSelection.select(row));
  }

  studentColumns:string[]=['imgsrc','name','appyear','lastloggedin','status','primaryadvisor','appointment','studentcategory'];
  studentSelectListData:any=null;
  initStudent(){
    let student:StudentElement[]=[
      {imgsrc:'../../../assets/img/sample-st1.png',name:'Nereida Acosta',appyear:'2018',lastloggedin:'12/02/2018',status:'Active',primaryadvisor:'John',appointment:'',studentcategory:''},
      {imgsrc:'../../../assets/img/sample-ad1.png',name:'Yu Jin Ahn',appyear:'2018',lastloggedin:'12/02/2018',status:'Active',primaryadvisor:'John',appointment:'',studentcategory:''},
      {imgsrc:'../../../assets/img/sample-ad1.png',name:'Yu Jin Ahn',appyear:'2018',lastloggedin:'12/02/2018',status:'Active',primaryadvisor:'John',appointment:'',studentcategory:''},
      {imgsrc:'../../../assets/img/sample-ad1.png',name:'Yu Jin Ahn',appyear:'2018',lastloggedin:'12/02/2018',status:'Active',primaryadvisor:'John',appointment:'',studentcategory:''}
    ]
    this.studentSelectListData = student;
  }
  //student Tasks
  toDoColumns1: string[] = ['select', 'color', 'dueDate','student', 'assignment'];
  taskSelection1 = new SelectionModel<StudentTasksElement>(true, []);
  toDoListData1:any=null;
  initToDoList1(){
    let tasks: StudentTasksElement[] = [
      {color:"#ff806e",dueDate:"12/02/2018",student:"Alli Apple",assignment:"Registration for 12/1/2018 SAT"},
      {color:"#ff806e",dueDate:"12/02/2018",student:"Sam H",assignment:"chad4"},
      {color:"#4dcb82",dueDate:"12/02/2018",student:"Alli Apple",assignment:"chad4"},
    ];
    this.toDoListData1 = new MatTableDataSource<StudentTasksElement>(tasks);

  }
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected1() {
    const numSelected = this.taskSelection1.selected.length;
    const numRows = this.toDoListData1.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle1() {
    this.isAllSelected1() ?
        this.taskSelection1.clear() :
        this.toDoListData1.data.forEach(row => this.taskSelection1.select(row));
  }
}
