import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'invoice-select-event',
  templateUrl: './invoice-select-event.component.html',
  styleUrls: ['./invoice-select-event.component.css']
})
export class InvoiceSelectEventComponent implements OnInit {

  //appionment
  checkAppInfo: any = {};

  constructor(public dialogRef: MatDialogRef<InvoiceSelectEventComponent>,
    @Inject(MAT_DIALOG_DATA) public appList: any = []) {
    this.checkAppInfo = appList[0];
  }

  ngOnInit() { }

  closeEvent(check): void {
    this.dialogRef.close(check);
  }

}
