import { Component, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'note-view',
  templateUrl: './note-view.component.html',
  styleUrls: ['./note-view.component.css']
})
export class NoteViewComponent implements OnChanges {

  //
  @Input() index: number;
  //id
  @Input() id: number;

  @Output() changeItem = new EventEmitter<any>();
  @Output() delectItem = new EventEmitter<number>();

  //
  noteDetail: any = [];
  //
  headerTitle: string;

  //
  textDetail: string = "";

  constructor(protected Dash: DashboardService,
    protected route: Router) {

  }

  ngOnChanges() {
    this.getDetail();
  }

  //
  getDetail() {
    this.Dash.GET(this.Dash.config.getNoteDetail, {
      NoteID: this.id
    }, true).then(result => {
      this.noteDetail = result;
      this.changeData();
      console.log(result);
    });
  }

  //
  editNote() {
    let params = {
      NoteID: this.id,
      title: this.headerTitle,
      text: this.textDetail
    };
    this.Dash.POST(this.Dash.config.editNote, params, true).then(result => {
      if (result)
        this.changeItem.emit({
          index: this.index,
          title: this.headerTitle
        });
    });
  }

  //
  removeNote() {
    this.Dash.POST(this.Dash.config.removeNote, {
      NoteID: this.id
    }, true).then(result => {
      if (!result) return;
      this.delectItem.emit(this.index);
    });
  }

  //
  changeData() {
    this.headerTitle = this.noteDetail.Title;
    this.textDetail = this.noteDetail.NoteText;
  }

}
