import { Component, OnInit } from '@angular/core';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { DashboardService } from '../../../service/dashboard.service';
import { ActivatedRoute } from '@angular/router';

import { AssignmentComponent } from '../assignment/assignment.component';

@Component({
  selector: 'app-assignment-view',
  templateUrl: './assignment-view.component.html',
  styleUrls: ['./assignment-view.component.css']
})
export class AssignmentViewComponent extends AssignmentComponent implements OnInit {
  //id
  id: string;
  //
  type: string;
  //
  assignment_detail: any = {};
  //
  headerTitle: string;
  //
  startDateValue: Date = new Date;
  //
  endDateValue: Date = new Date;

  //
  textDetail: string = "";

  //
  checkUser: any = [];

  constructor(protected Dash: DashboardService,
    protected route: ActivatedRoute) {
    super(Dash);
    this.getAssignmentTypeList();
    this.getAssignmentRecipients();
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.type = decodeURI(this.route.snapshot.paramMap.get('type'));
      this.id = decodeURI(this.route.snapshot.paramMap.get('id'));
      this.getAssignmentDetail();
    });
  }

  //
  getAssignmentDetail() {
    this.Dash.GET(this.Dash.config.getAssDetail, {
      AssignmentID: this.id
    }).then(result => {
      this.assignment_detail = result;
      this.changeData();
      console.log(result);
    });
  }

  //
  changeData() {
    this.headerTitle = this.assignment_detail.Title;
    this.startDateValue = this.assignment_detail.Start.slice(0, 10);
    this.endDateValue = this.assignment_detail.End.slice(0, 10);
    // this.allDay = this.assignment_detail.IsAllDay;
    // this.personCode = this.assignment_detail.TypeID;
    // this.siteCode = this.assignment_detail.Location; 
    this.textDetail = this.assignment_detail.Description;
  }

  //
  startDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.startDateValue = event.value;
  }

  //
  endDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.endDateValue = event.value;
  }

}
