import {
  Component,
  OnInit,
  Inject
} from '@angular/core';

import { MatDialogRef } from '@angular/material';
import { MAT_DIALOG_DATA } from '@angular/material';

import { DashboardService } from '../../../service/dashboard.service';

@Component({
  selector: 'invoice-delect',
  templateUrl: './invoice-delect.component.html',
  styleUrls: ['./invoice-delect.component.css']
})
export class InvoiceDelectComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<InvoiceDelectComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dash: DashboardService) { }


  ngOnInit() { }

  //info.type: 1 => , 0 => 
  closeEvent(info): void {
    this.dialogRef.close(info);
  }

  saveDraft(){
    this.closeEvent({
      type:1
    });
  }

  delectInvoice() {
    const params = {
      studentid: this.data.studentid,
      id: this.data.id,
      type: this.data.type, 
    };
    this.dash.postInvoice(this.dash.configPath.DeletePaymentOrInvoice, params, true).then(result => {
      this.closeEvent({
        info:result,
        type:0
      });
    });
  }




}
