import { Component, OnInit, ViewChild, OnChanges, DoCheck, Input, Output, EventEmitter, Inject, forwardRef } from '@angular/core';
import { MatSort, MatTableDataSource } from '@angular/material';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { MatDialog } from '@angular/material';

import { InvoiceDelectComponent } from './../invoice-delect/invoice-delect.component';

@Component({
  selector: 'invoice-list',
  templateUrl: './invoice-list.component.html',
  styleUrls: ['./invoice-list.component.css']
})

export class InvoiceListComponent implements OnInit, OnChanges, DoCheck {


  //
  selectedInfo: any = null;

  //
  @Input() invoiceList: any = [];
  //
  @Input() invoiceInfo: any = {};

  //
  @Input() displayedColumns: string[] = [];

  @Output() viewDetail = new EventEmitter<string>();

  @Output() editInvoice = new EventEmitter<string>();

  @Output() viewInvoice = new EventEmitter<string>();

  dataSource;

  @ViewChild(MatSort) sort: MatSort;

  constructor(public dialog: MatDialog,
    public dash: DashboardService,
    protected auth: AuthService) { }

  ngOnInit() { }

  ngOnChanges() {
    this.dataSource = new MatTableDataSource(this.invoiceList);
  }

  ngDoCheck() {
    this.dataSource = new MatTableDataSource(this.invoiceList);
    this.sortData();
  }

  sortData() {
    this.dataSource.sort = this.sort;
  }

  //
  openRemind(id, type) {
    const dialogRef = this.dialog.open(InvoiceDelectComponent, {
      data: {
        type: type,
        id: id,
        studentid: this.auth.userinfo.login_user.student.id
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      // result.type  0  1
      if (!result.type) {
        const index = this.requestIndex(type, id);
        this.invoiceList.splice(index, 1);
        this.dataSource = new MatTableDataSource(this.invoiceList);
        this.invoiceInfo.TotalPayment = result.info.TotalPayment;
        this.invoiceInfo.TotalInvociced = result.info.TotalInvociced;
        this.invoiceInfo.TotalDue = result.info.TotalDue;
      }
    });
  }

  delectInvoice(id, type, flag = true) {
    const params = {
      studentid: this.auth.userinfo.login_user.student.id,
      id: id, 
      type: type,
    };
    this.dash.postInvoice(this.dash.configPath.DeletePaymentOrInvoice, params, true).then(result => {
      const index = this.requestIndex(type, id);
      this.invoiceList.splice(index, 1);
      this.dataSource = new MatTableDataSource(this.invoiceList);
      if(!flag) return;
      this.invoiceInfo.TotalPayment = result.TotalPayment;
      this.invoiceInfo.TotalInvociced = result.TotalInvociced;
      this.invoiceInfo.TotalDue = result.TotalDue;
    });
  }

  //1 => payment, 2 => invoice
  requestIndex(type, id) {
    if (type == 2) {
      for (let i = 0; i < this.invoiceList.length; i++) {
        if (this.invoiceList[i].InvoiceID == id)
          return i;
      }
    }
    for (let i = 0; i < this.invoiceList.length; i++) {
      if (this.invoiceList[i].PaymentID == id)
        return i;
    }
  }


}
