import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../service/auth.service';
import { AccountService } from '../../service/account.service';
import { FactoryService } from '../../service/factory.service';

@Component({
  selector: 'app-fed-iframe',
  templateUrl: './fed-iframe.component.html',
  styleUrls: ['./fed-iframe.component.css']
})
export class FedIFrameComponent implements OnInit {

  public menu;
  constructor(public auth: AuthService, public router:Router ,public route: ActivatedRoute, public accountService: AccountService) { 
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      let ID = +params['ID'];
      
      for(let v of this.auth.userinfo.login_user.menu.list){
        if(v.ID==ID){
          this.menu=v;
          break;
        }
      }
      
    });
  }
}
