import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { ActivatedRoute } from '@angular/router';

import { AppointmentComponent } from './../appointment/appointment.component'
@Component({
  selector: 'app-appointment-view',
  templateUrl: './appointment-view.component.html',
  styleUrls: ['./appointment-view.component.css']
})
export class AppointmentViewComponent extends AppointmentComponent implements OnInit {

  //id
  id: string;
  //
  type: string;
  //
  appiontment_detail: any = {};
  //
  headerTitle: string;
  //
  startDateValue: Date = new Date;
  //
  startTimeCode: string = "00";
  //
  endDateValue: Date = new Date;
  //
  endTimeCode: string = "00";
  //
  allDay: number = 0;
  //
  personCode: number = 1;
  //
  siteCode: string = "";

  //
  textDetail: string = "";

  //
  timeJson: any = [];

  //
	checkUser: any = [];

  constructor(protected Dash: DashboardService,
    protected route: ActivatedRoute) {
    super(Dash);
    this.getTypeList();
    this.getRecipientList();
  }

  ngOnInit() {
    this.changeTimeJson();
    this.route.params.subscribe(params => {
      this.type = decodeURI(this.route.snapshot.paramMap.get('type'));
      this.id = decodeURI(this.route.snapshot.paramMap.get('id'));
      this.getDetail();
    });
  }

  //
  changeData() {
    this.headerTitle = this.appiontment_detail.Title;
    this.startDateValue = this.appiontment_detail.Start.slice(0, 10);
    this.startTimeCode = this.appiontment_detail.Start.slice(11, 13);
    this.endDateValue = this.appiontment_detail.End.slice(0, 10);
    this.endTimeCode = this.appiontment_detail.End.slice(11, 13);
    this.allDay = this.appiontment_detail.IsAllDay;
    this.personCode = this.appiontment_detail.TypeID;
    this.siteCode = this.appiontment_detail.Location;
    this.textDetail = this.appiontment_detail.Description;
  }

  //
  getDetail() {
    this.Dash.GET(this.Dash.config.getAppDetail, {
      AppointmentID: this.id
    }).then(result => {
      this.appiontment_detail = result;
      this.changeData();
      console.log(result);
    });
  }

  //
  changeApp() {
    let params = {
      AppointmentID: this.id,
      // StudentID:,
      // Start: ,
      // End:,
      Title: this.headerTitle,
      Description: this.textDetail,
      Location: this.siteCode,
      // StatusID:,
      TypeID: this.personCode,
      IsAllDay: this.allDay,
      // IsComplete:,
      // GroupNotifiedUserTypes:,
    };
    this.Dash.POST(this.Dash.config.editApp, params).then(result => {
      console.log(result);
      if (result) alert("!");
    });
  }

  //
  startDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.startDateValue = event.value;
  }

  //
  endDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.endDateValue = event.value;
  }

  //json
  changeTimeJson() {
    this.timeJson = [
      { name: "0:00", value: "00" },
      { name: "1:00", value: "01" },
      { name: "2:00", value: "02" },
      { name: "3:00", value: "03" },
      { name: "4:00", value: "04" },
      { name: "5:00", value: "05" },
      { name: "6:00", value: "06" },
      { name: "7:00", value: "07" },
      { name: "8:00", value: "08" },
      { name: "9:00", value: "09" },
      { name: "10:00", value: "10" },
      { name: "11:00", value: "11" },
    ];
  }

  removeAppDraft(){}

}
