import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';

import { SaveDraftService } from './../../common/service/save-draft.service';

import { MessageComponent } from './../message/message.component'


@Component({
  selector: 'app-message-create',
  templateUrl: './message-create.component.html',
  styleUrls: ['./message-create.component.css']
})
export class MessageCreateComponent extends MessageComponent implements OnInit {

  //  0=>email 1=>text
  msgType: number | string = 0;
  //id
  msgId:number | string;
  //id
  converId:number | string;
  //id
  studentId: number = null;
  //id
  senderId: number = null;

  //ID
  recipientList: any = [];
  //
  subjectText: string = "";
  //
  textDetail: string = "";

  constructor(protected Dash: DashboardService,
    protected location: Location,
    protected auth: AuthService,
    protected router: Router,
    protected route: ActivatedRoute,
    public saveDraft: SaveDraftService) { 
    super(Dash, location, auth, router);
  }

  ngOnInit() {
    this.getRecipients();
    this.getSessionInfo();
    this.route.params.subscribe(params => {
      this.msgType = decodeURI(this.route.snapshot.paramMap.get('type'));
      this.msgId = decodeURI(this.route.snapshot.paramMap.get('msgid'));
      this.converId = decodeURI(this.route.snapshot.paramMap.get('converid'));
    });
  }

  //
	saveDraftData() {
		// if (this.view_type == 1 || this.view_message.msg.length == 0) return;

		this.saveDraft.saveDraft({
			ConversationID: this.converId,
			MessageID: this.msgId,
			Sender: this.senderId,
			RecipientList: this.recipientList,
			subject: this.subjectText,
			text: this.textDetail,
		});
  }
  
  sendDraftMessage() {
		let option: any = {
      ConversationID: this.converId,
			MessageID: this.msgId,
			StudentID: this.studentId,
			Sender: this.senderId,
			RecipientList: this.recipientList,
			subject: this.subjectText,
			text: this.textDetail
		};
		this.Dash.POST(this.Dash.config.sendMsgDraft, option, true).then(result => {
			this.router.navigateByUrl(`/message/view/1/${this.converId}`);
		});
  }
  
  //
	removeDraft() {
		this.Dash.POST(this.Dash.config.removeDraftMsg, {
			ConversationID: this.converId
		},true).then(result => {
			this.router.navigateByUrl(`/message`);
		});
	}


  //
  backPage() {
    history.back();
  }
  

}
