import { Component, OnInit, ViewChild,ElementRef } from '@angular/core';
import { MatSort, MatTableDataSource } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';

export interface PeriodicElement {
  images: string;
  name: string;
  email: string;
  last_logged_in: string;
  status: string;
  app_year: string;
  school: string;
  state: string;
  country: string;
  student_categories: string;
  advisor: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  {
    images: "./assets/img/sample-ad1.png",
    name: "Tao",
    email: "853017233@qq.com",
    last_logged_in: "",
    status: "Active",
    app_year: "2019",
    school: "PMC High School",
    state: "CA",
    country: "US",
    student_categories: "wang's family",
    advisor: "Kuan, April",
  }
];

@Component({
  selector: 'app-select-student',
  templateUrl: './select-student.component.html',
  styleUrls: ['./select-student.component.css']
})

export class SelectStudentComponent implements OnInit {
  first_name: string = "";
  app_year: any = [];
  student_cate: any = [];
  state: any = [];
  last_name: string = "";
  status: any = [];
  high_school: string = "";
  country: any = [];
  advisor: any = [];

  // fake data from server ====
  formData={
    app_year: [ 2019,2018,2017,2016,2015],
    student_cate: [{id:1,value:'1',name:'test1'}],
    state: [{id: 3, value: "AL", name: "Alabama"},{id: 8, value: "CA", name: "California"}],
    status: ['Student','Prospect','Active','Archived'],
    country: [{id: 60, value: "AU", name: "Australia"},{id: 61, value: "BE", name: "Belgium"}],
    advisor: [{id:1,value:'1',first_name:'Weiqi',last_name:'Wang'},{id:2,value:'2',first_name:'Hans',last_name:'Chen'},]
  }
  // fake data from server ====

  //
  family:any = 1;
  student:any = 2;
  prospect:any = 3;
  active:any = 8;
  archived:any = 4;
  kuan:any = 5;
  qin:any = 6;

  displayedColumns: string[] = ['select','images', 'name', 'email', 'last_logged_in', 'app_year', 'school', 'state', 'country', 'student_categories', 'advisor'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);
  selection = new SelectionModel(true, []);
  
  @ViewChild(MatSort) sort: MatSort;

  ngOnInit() {
    this.dataSource.sort = this.sort;
  }
  
  selectAll(item,id){
    if(item.options.first._selected){
      this.setValueAll(id);
      // setTimeout(()=>{console.log(item)},1500);
    }else{
      this.resetAll(id);
    }
    
  }
  selectOne(item,id){
    let opt = item.options._results
    this._compareValue(id)?(this.setValueAll(id),opt[0].select()):opt[0]._selected?(this.removeFirstValue(id),opt[0].deselect()):null;
    // console.log(this._compareValue(id));
  }
  removeFirstValue(id){
    if(id==0) this.app_year.splice(0,1);
    if(id==1) this.student_cate.splice(0,1);
    if(id==2) this.state.splice(0,1);
    if(id==3) this.status.splice(0,1);
    if(id==4) this.country.splice(0,1);
    if(id==5) this.advisor.splice(0,1);
  }
  _compareValue(id){
    let arr=[];
    let originLength;
    if(id==0) {arr=this.app_year; originLength=this.formData.app_year.length;}
    if(id==1) {arr=this.student_cate; originLength=this.formData.student_cate.length;}
    if(id==2) {arr=this.state; originLength=this.formData.state.length;}
    if(id==3) {arr=this.status; originLength=this.formData.status.length;}
    if(id==4) {arr=this.country; originLength=this.formData.country.length;}
    if(id==5) {arr=this.advisor; originLength=this.formData.advisor.length;}
    
    arr.includes('all')?arr.length-=1:null;
    return arr.length==originLength;
  }
  setValueAll(id){
    
    if(id==0) { 
      this.app_year=['all'];
      for(let i=0; i<this.formData.app_year.length; i++){
        this.app_year.push(this.formData.app_year[i]);
      }
    }
    if(id==1) {
      this.student_cate=['all'];
      for(let i=0; i<this.formData.student_cate.length; i++){
        this.student_cate.push(this.formData.student_cate[i].value);
      }
    }
    if(id==2) {
      this.state=['all'];
      for(let i=0; i<this.formData.state.length; i++){
        this.state.push(this.formData.state[i].value);
      }
    }
    if(id==3) {
      this.status=['all'];
      for(let i=0; i<this.formData.status.length; i++){
        this.status.push(this.formData.status[i]);
      }
    }
    if(id==4) {
      this.country=['all'];
      for(let i=0; i<this.formData.country.length; i++){
        this.country.push(this.formData.country[i].value);
      }
    }
    if(id==5) {
      this.advisor=['all'];
      for(let i=0; i<this.formData.advisor.length; i++){
        this.advisor.push(this.formData.advisor[i].value);
      }
    }

    
  }
  resetAll(id){
    if(id==0)this.app_year=[];
    if(id==1)this.student_cate=[];
    if(id==2)this.state=[];
    if(id==3)this.status=[];
    if(id==4)this.country=[];
    if(id==5)this.advisor=[];
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

}
