import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { LoadingService } from './../../../shared/loading/service/loading.service';

import { MatDialog } from '@angular/material';
import { InvoiceRecordComponent } from './../invoice-record/invoice-record.component';

@Component({
  selector: 'invoice-summary',
  templateUrl: './invoice-summary.component.html',
  styleUrls: ['./invoice-summary.component.css']
})

export class InvoiceSummaryComponent implements OnInit {

  //
  invoiceList: any = [];
  //
  invoiceInfo: any = {};
  //
  invoiceDetail: any = {};
  //
  invoiceHistory: any = [];

  //
  displayedColumns: string[] = ['Reference', 'Date', 'Recipient', 'Advisor', 'Description', 'Payment', 'Invociced'];

  //
  record_success: boolean = false;

  //
  isOpenDetail: boolean = false;

  paymentList: any = [];

  constructor(public dialog: MatDialog,
    public dash: DashboardService,
    protected auth: AuthService,
    public loading: LoadingService) { }

  ngOnInit() {
    this.loadAllAjax();
  }

  loadAllAjax() {
    this.loading.loading(true);
    Promise.all([this.getInvoiceList(),
    this.getInvoiceHistory(),
    this.getPaymentList()]).then(() => {
      this.loading.loading(false);
    })
  }

  //
  getInvoiceList() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetStudentInvoices, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        // console.log(result);
        this.invoiceInfo = result;
        this.invoiceList = result.PaymentInvoiceModels;
        resolve();
      });
    });
  }

  //
  getInvoiceDetail(id) {
    this.dash.getInvoice(this.dash.configPath.GetInvoiceById, {
      invoiceid: id
    }, true).then(result => {
      this.invoiceDetail = result;
      // console.log(this.invoiceDetail);      
      this.isOpenDetail = true;
    });
  }

  //
  getInvoiceHistory() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetHistoryBillNote, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        this.invoiceHistory = result.PaymentInvoiceModels;
        resolve();
      });
    })
  }

  //
  getPaymentList() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetPaymentType, {}).then(result => {
        this.paymentList = result;
        resolve();
      });
    });
  }

  //
  openSuccessRecord() {
    this.record_success = true;
    setTimeout(() => {
      this.record_success = false;
    }, 3000)
  }

  //
  openRecord() {
    const dialogRef = this.dialog.open(InvoiceRecordComponent, {
      width: '600px',
      data: {
        paymentList: this.paymentList,
        studenid: this.auth.userinfo.login_user.student.id
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result){
        this.invoiceList.unshift(result.PaymentInvoiceModels[0]);
        this.invoiceInfo.TotalPayment = result.TotalPayment;
        this.invoiceInfo.TotalInvociced = result.TotalInvociced;
        this.invoiceInfo.TotalDue = result.TotalDue;
        this.openSuccessRecord();
      }
    });
  }
  

}
