import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { HomepageComponent } from './homepage/homepage.component';
import { ShortcutComponent } from './shortcut/shortcut.component';
import { IndexComponent } from './index/index.component';
import { MessageComponent } from './message/message/message.component';

import { AppointmentComponent } from './appointment/appointment/appointment.component';
import { AppointmentViewComponent } from './appointment/appointment-view/appointment-view.component';
import { AppointmentCreateComponent } from './appointment/appointment-create/appointment-create.component';

import { AssignmentComponent } from './assignment/assignment/assignment.component';
import { AssignmentViewComponent } from './assignment/assignment-view/assignment-view.component';
import { AssignmentCreateComponent } from './assignment/assignment-create/assignment-create.component';

import { CalendarComponent } from './calendar/calendar/calendar.component';
import { CalendarViewComponent } from './calendar/calendar-view/calendar-view.component';
import { CalendarEditComponent } from './calendar/calendar-edit/calendar-edit.component';

import { StorageComponent } from './storage/storage.component';
import { ReportComponent } from './report/report.component';
import { ActivityComponent } from './activity/activity.component';

import { StatisticHomeComponent } from './statistic/statistic-home/statistic-home.component';
import { StatisticDetailComponent } from './statistic/statistic-detail/statistic-detail.component';

import { InvoiceComponent } from './invoice/invoice/invoice.component';
import { InvoiceSummaryComponent } from './invoice/invoice-summary/invoice-summary.component';
import { InvoiceListComponent } from './invoice/invoice-list/invoice-list.component';
import { InvoiceDetailComponent } from './invoice/invoice-detail/invoice-detail.component';
import { InvoiceCreateComponent } from './invoice/invoice-create/invoice-create.component';
import { InvoiceSelectEventComponent } from './invoice/invoice-select-event/invoice-select-event.component';
import { InvoiceRecordComponent } from './invoice/invoice-record/invoice-record.component';
import { InvoiceDelectComponent } from './invoice/invoice-delect/invoice-delect.component';
import { InvoiceSelectBillComponent } from './invoice/invoice-select-bill/invoice-select-bill.component';
import { InvoiceHistoryComponent } from './invoice/invoice-history/invoice-history.component';

import { NoteComponent } from './note/note/note.component';
import { NoteViewComponent } from './note/note-view/note-view.component';
import { NoteCreateComponent } from './note/note-create/note-create.component';


import { GuruComponent } from './guru/guru.component';
import { TaskComponent } from './task/task.component';
import { ToolsComponent } from './tools/tools.component';

import { DashboardComponent } from './dashboard.component';
import { MessageViewComponent } from './message/message-view/message-view.component';
import { MessageCreateComponent } from './message/message-create/message-create.component';
import { SelectStudentComponent } from './select-student/select-student.component';
import { FedIFrameComponent } from './fed-iframe/fed-iframe.component';

// common components
import { UserSelectComponent } from './common/component/user-select/user-select.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: DashboardComponent,
        children: [
          { path: 'Home/AdvisorHome', component: HomepageComponent },
          { path: 'Home/AdvisorLaunchpad', component: ShortcutComponent },
          { path: 'account/SelectStudent', component: SelectStudentComponent },
          { path: 'dashboard/index', component: IndexComponent },
          {
            path: 'message',
            component: MessageComponent,
            children: [
              { path: 'view/:type/:id', component: MessageViewComponent },
            ]
          },
          { path: 'message/create/:type/:msgid/:converid', component: MessageCreateComponent },
          {
            path: 'appointment',
            component: AppointmentComponent,
            children: [
              { path: 'view/:type/:id', component: AppointmentViewComponent }
            ]
          },
          { path: 'appointment/create', component: AppointmentCreateComponent },
          {
            path: 'assignment',
            component: AssignmentComponent,
            children: [
              { path: 'view/:type/:id', component: AssignmentViewComponent },
            ]
          },
          { path: 'assignment/create', component: AssignmentCreateComponent },
          { path: 'calendar', component: CalendarComponent },
          { path: 'calendar/edit', component: CalendarEditComponent },
          { path: 'storage', component: StorageComponent },
          { path: 'report', component: ReportComponent },
          { path: 'activity', component: ActivityComponent },

          { path: 'statistic', component: StatisticHomeComponent },
          { path: 'statistic/detail', component: StatisticDetailComponent },

          {
            path: 'invoice',
            component: InvoiceComponent,
            children: [
              {
                path: 'summary',
                component: InvoiceSummaryComponent,
              },
              {
                path: 'create',
                component: InvoiceCreateComponent,
              }
            ]
          },
          { path: 'note', component: NoteComponent, },
          { path: 'note/create', component: NoteCreateComponent },

          { path: 'guru', component: GuruComponent },
          { path: 'tools', component: ToolsComponent },
          { path: 'task', component: TaskComponent },
          { path: 'FedIFrame/:ID', component: FedIFrameComponent },
        ]
      }
    ]
    )
  ],
  declarations: [
    DashboardComponent,
    HomepageComponent,
    IndexComponent,
    MessageComponent,
    AppointmentComponent,
    AppointmentViewComponent,
    AppointmentCreateComponent,
    AssignmentComponent,
    AssignmentViewComponent,
    AssignmentCreateComponent,
    CalendarComponent,
    CalendarViewComponent,
    CalendarEditComponent,
    StorageComponent,
    ReportComponent,
    ActivityComponent,

    StatisticHomeComponent,
    StatisticDetailComponent,

    InvoiceComponent,
    InvoiceSummaryComponent,
    InvoiceListComponent,
    InvoiceDetailComponent,
    InvoiceCreateComponent,
    InvoiceHistoryComponent,
    InvoiceRecordComponent,
    InvoiceDelectComponent,
    InvoiceSelectEventComponent,
    InvoiceSelectBillComponent,

    NoteComponent,
    NoteViewComponent,
    NoteCreateComponent,
    GuruComponent,
    ToolsComponent,
    TaskComponent,
    ShortcutComponent,
    MessageViewComponent,
    MessageCreateComponent,
    SelectStudentComponent,
    FedIFrameComponent,

    UserSelectComponent
  ],
  entryComponents: [
    CalendarViewComponent,

    InvoiceRecordComponent,
    InvoiceDelectComponent,
    InvoiceSelectEventComponent,
    InvoiceSelectBillComponent
  ]
})
export class DashboardModule { }
