import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { DashboardService } from '../../../service/dashboard.service';

@Component({
  selector: 'invoice-record',
  templateUrl: './invoice-record.component.html',
  styleUrls: ['./invoice-record.component.css']
})
export class InvoiceRecordComponent implements OnInit {

  paymentList: any = [];
  method: any = {};
  paymentDate: Date = new Date();
  record: string = "";
  amount: number = 0;

  constructor(public dialogRef: MatDialogRef<InvoiceRecordComponent>,
    public dash: DashboardService, 
    @Inject(MAT_DIALOG_DATA) public data: any) {
    this.paymentList = this.data.paymentList;
    this.method = this.data.paymentList[0];
  }

  ngOnInit() { }

  closeEvent(success): void {
    this.dialogRef.close(success);
  }

  saveEvent() {
    let params = {
      studentid: this.data.studenid,
      Date: this.paymentDate,
      Description: this.record,
      Amount: this.amount,
      PaymentTypeID: this.method.ID
    };
    this.dash.postInvoice(this.dash.configPath.CreatePayment, params, true).then(result => {
      // console.log(params);
      this.closeEvent(result);
    });
  }




}

