import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';

@Component({
  selector: 'app-note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.css']
})

export class NoteComponent implements OnInit {

  //
  noteList = [];
  
  //
  showNoneMessage: boolean = true;

  //
  viewIndex:number;

  //id
  viewId:number;
  
  constructor(protected Dash: DashboardService) {}
  
  ngOnInit() {
    this.getNotes();
  }

  //
  getNotes() {
    this.Dash.GET(this.Dash.config.getNoteList).then(result => {
      this.noteList = result; 
    }); 
  }

  //
  openDetail(index,id){
    this.showNoneMessage = false;
    this.viewIndex = index;
    this.viewId = id;
  }

  //
  changeItem(info){
    let {index} = info;
    let {title} = info;
    this.noteList[index].Title = title;
  }

  //
  delectItem(index){
    this.showNoneMessage = true;
    this.noteList.splice(index,1);
  }


}
