import {
  Component,
  OnInit
} from '@angular/core';

import {MatDatepickerInputEvent} from '@angular/material/datepicker';

@Component({
  selector: 'app-calendar-edit',
  templateUrl: './calendar-edit.component.html',
  styleUrls: ['./calendar-edit.component.css']
})

export class CalendarEditComponent implements OnInit {

  //
  headerTitle:string;
  //
  startDateValue:Date = new Date;
  //
  startTimeCode:string = "am0";
  //
  endDateValue:Date = new Date;
  //
  endTimeCode:string = "am0";
  //
  personCode:string = "person";
  //
  siteCode:string = "site1"

  //
  textDetail:string = "";

  constructor() { }

  ngOnInit() {

  }

  //
  startDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.startDateValue = event.value;
  }

  //
  endDateChange(type: string, event: MatDatepickerInputEvent<Date>) {
    this.endDateValue = event.value;
  }

  backPage(){
    history.back();
  }

}