import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { LoadingService } from './../../../shared/loading/service/loading.service';

import { InvoiceSelectBillComponent } from './../invoice-select-bill/invoice-select-bill.component';
import { InvoiceSelectEventComponent } from './../invoice-select-event/invoice-select-event.component';
@Component({
  selector: 'invoice-create',
  templateUrl: './invoice-create.component.html',
  styleUrls: ['./invoice-create.component.css']
})

export class InvoiceCreateComponent implements OnInit {

  //
  isViewDraft: boolean = false;

  //
  isCheckList: boolean = false;

  //
  draftList: any = [];

  //ID
  invoiceId: number = 0;

  //
  invoiceDetail: any = {};

  //
  displayedColumns: string[] = ['Reference', 'Date', 'Recipient', 'Description', 'Amount'];

  //
  invoiceNum: number = 0;

  //
  billInfo: any = {};

  //
  advistorList: any = [];

  //
  checkAdvistor: any = {};

  //
  dueDate: Date = new Date;

  //
  BillingNotes: string = "";

  //
  addMessage: string = "";

  //appionment
  appList: any = [];

  //
  amount: number = 0;

  //
  billList: any = [];

  //
  invoiceItemList: any = [];

  //
  invoiceHistory: any = [];

  constructor(public dialog: MatDialog,
    public dash: DashboardService,
    protected auth: AuthService,
    public loading: LoadingService) { }

  ngOnInit() {
    this.loadAllAjax();
  }

  loadAllAjax() {
    this.loading.loading(true);
    Promise.all([this.getInvoiceNumber(),
    this.getAdvistorList(),
    this.getDraftList(),
    this.getAppionments(),
    this.getBillList(),
    this.getInvoiceHistory()]).then(() => {
      this.loading.loading(false);
    })
  }

  //
  getInvoiceNumber() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetInvoiceNumber, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        // console.log(result);
        this.invoiceNum = result;
        resolve();
      });
    });
  }

  //
  getAdvistorList() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetStudentAdvisors, {
        advisorid: this.auth.userinfo.login_user.AdvisorID
      }).then(result => {
        // console.log(result);
        this.advistorList = result;
        this.checkAdvistor = this.advistorList[0];
        resolve();
      });
    });

  }

  //
  getDraftList() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetStudentDraft, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        this.draftList = result;
        console.log(this.draftList);
        resolve();
      });
    });
  }

  //appionments
  getAppionments() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetStudentAppointments, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        // console.log(result);
        this.appList = result;
        resolve();
      });
    });
  }

  //
  getBillList() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetStudentBillingContacts, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        this.billList = result;
        this.billInfo = result[0];
        // console.log(result);
        resolve();
      });
    });
  }

  //
  getInvoiceHistory() {
    return new Promise((resolve) => {
      this.dash.getInvoice(this.dash.configPath.GetHistoryBillNote, {
        studentid: this.auth.userinfo.login_user.student.id
      }).then(result => {
        this.invoiceHistory = result.PaymentInvoiceModels;
        resolve();
      });
    })
  }

  //
  saveDraft() {
    let params = {
      InvoiceID: this.invoiceId,
      StudentID: this.auth.userinfo.login_user.student.id,
      StudentInvoiceNumber: this.invoiceNum,
      DateCreated: this.dueDate,
      BillToBillingContactId: this.billInfo.BillingPersonID,
      AdvisorPersonId: this.checkAdvistor.PersonID,
      BillingNotes: this.BillingNotes,
      CustomSalutation: this.addMessage,
      TotalPrice: this.amount,
      AdvisorFirstName: this.checkAdvistor.FirstName,
      AdvisorLastName: this.checkAdvistor.LastName,
      AdvisorAddress: this.checkAdvistor.Address1,
      AdvisorEmail: this.checkAdvistor.Email,
      FirstName: this.billInfo.FirstName,
      LastName: this.billInfo.LastName,
      Address1: this.billInfo.Address1,
      City: this.billInfo.City,
      State: this.billInfo.State,
      PostalCode: this.billInfo.PostalCode,
      Email: this.billInfo.Email,
      InvoiceItems: this.invoiceItemList
    };
    // console.log(params);
    this.dash.postInvoice(this.dash.configPath.OperInvoice, params, true).then(result => {
      if (this.invoiceId == 0)
        this.draftList.PaymentInvoiceModels.unshift(result.PaymentInvoiceModels[0]);
      this.isCheckList = true;
      this.clearNow();
    });
  }

  selectBill() {
    const dialogRef = this.dialog.open(InvoiceSelectBillComponent, {
      width: '700px',
      data: this.billList
    });
    dialogRef.afterClosed().subscribe(result => {
      if (!result) return;
      // console.log(result);
      this.billInfo = result;
    });
  }

  SelectItem() {
    const dialogRef = this.dialog.open(InvoiceSelectEventComponent, {
      width: '700px',
      data: this.appList
    });
    dialogRef.afterClosed().subscribe(result => {
      if (!result) return;
      this.invoiceItemList.push({
        Description: result.Description,
        BillableQuantity: 1,
        BillableRate: 0,
        BillableTotal: 0,
        Disabled: false,
      });
      this.totalAmount();
    });
  }

  //
  preview() {
    if (this.invoiceId != 0) {
      this.viewInvoice({ InvoiceID: this.invoiceId });
      return;
    }

    this.invoiceDetail = {
      StudentInvoiceNumber: this.invoiceNum,
      AdvisorFirstName: this.checkAdvistor.FirstName,
      AdvisorLastName: this.checkAdvistor.LastName,
      AdvisorAddress: this.checkAdvistor.Address1,
      AdvisorEmail: this.checkAdvistor.Email,
      FirstName: this.billInfo.FirstName,
      LastName: this.billInfo.LastName,
      City: this.billInfo.City,
      State: this.billInfo.State,
      PostalCode: this.billInfo.PostalCode,
      Date: this.dueDate,
      invoiceItems: this.invoiceItemList,
      DateLastSent: new Date(),
    }
    this.isViewDraft = true;

  }

  //
  editNowView() {
    if (this.invoiceId != 0) {
      this.editInvoice({ InvoiceID: this.invoiceId });
      return;
    }

    this.isCheckList = false;
    this.isViewDraft = false;
  }

  //
  changeState(index) {
    this.invoiceItemList[index].Disabled = !this.invoiceItemList[index].Disabled;
    if (this.invoiceItemList[index].Disabled)
      this.totalAmount();
  }

  //
  delectItem(index) {
    this.invoiceItemList.splice(index, 1);
    this.totalAmount();
  }

  //
  totalAmount() {
    this.amount = 0;
    for (let item of this.invoiceItemList) {
      this.amount += parseFloat(item.BillableTotal);
    }
  }

  //
  editInvoice(data) {
    console.log(data);
    this.dash.getInvoice(this.dash.configPath.GetInvoiceById, {
      invoiceid: data.InvoiceID
    }, true).then(result => {
      this.invoiceId = result.InvoiceID;
      this.invoiceNum = result.StudentInvoiceNumber;
      this.dueDate = result.Date.substr(0, 10);
      this.BillingNotes = result.BillingNotes;
      this.invoiceItemList = result.invoiceItems;
      this.addMessage = result.CustomSalutation;
      this.amount = result.Total;
      for (let item of this.billList) {
        if (item.BillingPersonID == result.BillToBillingContactId)
          this.billInfo = item;
      }
      for (let item of this.advistorList) {
        if (item.PersonID == result.AdvisorPersonId)
          this.checkAdvistor = item;
      }
      this.isCheckList = false;
      this.isViewDraft = false;
    });
  }

  //
  clearNow() {
    this.invoiceId = 0;
    // this.invoiceNum = ;
    this.dueDate = new Date();
    this.BillingNotes = "";
    this.invoiceItemList = [];
    this.addMessage = "";
    this.amount = 0;
    this.billInfo = this.billList[0];
    this.checkAdvistor = this.advistorList[0];
  }

  //
  viewInvoice(data) {
    this.dash.getInvoice(this.dash.configPath.GetInvoiceById, {
      invoiceid: data.InvoiceID
    }, true).then(result => {
      console.log(result);

      this.invoiceDetail = result;
      this.invoiceId = result.InvoiceID;
      this.isViewDraft = true;
    });
  }

  //
  sendInvoice() {
    this.dash.postInvoice(this.dash.configPath.EmailInvoice, {
      invoiceID: this.invoiceId,
      studentid: this.auth.userinfo.login_user.student.id
    }, true).then(result => {
      // console.log(result);
      this.clearNow();
      this.isViewDraft = false;
      this.isCheckList = true;
    })
  }


}