import { Component, OnInit } from '@angular/core';
import { Chart } from 'angular-highcharts';

@Component({
  selector: 'app-statistic',
  templateUrl: './statistic-home.component.html',
  styleUrls: ['./statistic-home.component.css']
})
export class StatisticHomeComponent implements OnInit {
  admissionsInfo: any = new Chart();
  summaryInfo: any = new Chart();
  deadlineTypeInfo: any = new Chart();
  methodInfo: any = new Chart();
  nextStepInfo: any = new Chart();
  flag: boolean = false;
  constructor() { }

  ngOnInit() {
    this.drawCharts();
  }

  drawCharts() {
    this.admissionsInfo = new Chart({
      chart: {
        type: 'bar'
      },
      title: {
        text: null
      },
      xAxis: {
        categories: ['Enrolled in College', 'Admitted to College', 'Applied to College'],
      },
      yAxis: {
        min: 0,
        allowDecimals: false,
        title: {
          text: 'Number of Students',
          align: 'high'
        }
      },
      legend: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        series: {
          dataLabels: {
            enabled: true,
            allowOverlap: true
          },
          animation: false
        }
      },
      series: [{
        name: "",
        data: [1, 1, 0],
        color: "#8dbe48"
      }]
    });

    this.summaryInfo = new Chart({
      title: {
        text: null,
      },
      tooltip: {
        headerFormat: '{series.name}<br>',
        pointFormat: '{point.name}: <b>{point.percentage:.1f}%</b>'
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        pie: {
          dataLabels: {
            enabled: true,
            distance: -50,
          },
          startAngle: -90, // 
          endAngle: 90,    // 
          center: ['50%', '75%']
        }
      },
      colors: ['#8dbe48'],
      series: [{
        type: 'pie',
        name: 'Application<br>Summary',
        innerSize: '50%',
        data: [
          ['admitted(5)', 5],
        ]
      }]
    });

    this.deadlineTypeInfo = new Chart({
      chart: {
        type: 'bar',
      },
      title: {
        text: null
      },
      xAxis: {
        categories: ['Regular', 'Priority', 'Early Action 1',
          'Early Action 2', 'Early Decision 1', 'Early Decision 2', 'Restrictive Early Action', 'Other'],
      },
      yAxis: {
        min: 0,
        allowDecimals: false,
        title: {
          text: 'Number of Applications',
          align: 'high'
        }
      },
      legend: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        series: {
          dataLabels: {
            enabled: true,
            allowOverlap: true
          },
          animation: false
        }
      },
      series: [{
        name: "",
        data: [2, 0, 1, 1, 0, 0, 0, 1],
        color: "#8dbe48"
      }]
    });

    this.methodInfo = new Chart({
      chart: {
        type: 'bar',
      },
      title: {
        text: null
      },
      xAxis: {
        categories: ['Common Application', 'Coalition Application', 'Institution Application'],
      },
      yAxis: {
        min: 0,
        allowDecimals: false,
        title: {
          text: 'Number of Applications',
          align: 'high'
        }
      },
      legend: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        series: {
          dataLabels: {
            enabled: true,
            allowOverlap: true
          },
          animation: false
        }
      },
      series: [{
        name: "",
        data: [3, 0, 2],
        color: "#8dbe48"
      }]
    });

    this.nextStepInfo = new Chart({
      chart: {
        type: 'bar',
      },
      title: {
        text: null
      },
      xAxis: {
        categories: ['4 Year', '2 Year', 'Gap', 'Military', 'Workforce', 'Volunteer/Service', 'Undecided'],
      },
      yAxis: {
        min: 0,
        allowDecimals: false,
        title: {
          text: 'Number of Applications',
          align: 'high'
        }
      },
      legend: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      plotOptions: {
        series: {
          dataLabels: {
            enabled: true,
            allowOverlap: true
          },
          animation: false
        }
      },
      series: [{
        name: "",
        data: [1, 0, 0, 0, 0, 0, 2],
        color: "#8dbe48"
      }]
    });

  }
}