import { Component, OnInit } from '@angular/core';
import { DashboardService } from '../../../service/dashboard.service';
import { AuthService } from '../../../service/auth.service';
import { Router } from '@angular/router';

import { Location } from '@angular/common';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {

  //
  msgList = [];

  //
  recipientsList: any = {
    student: [],
    advisor: [],
    connection: []
  };

  //
  checkRecipients: any = {
    student: [],
    advisor: [],
    connection: []
  };

  //
  showNoneMessage: boolean = true;

  //
  inboxFlag: boolean = true;

  //
  draftFlag: boolean = true;

  //id
  studentId: number = null;

  //ID
  senderId: number = null;

  constructor(protected Dash: DashboardService,
    protected location: Location,
    protected auth: AuthService,
    protected router: Router) { }

  ngOnInit() {
    this.getConversations();
    this.getSessionInfo();
    if (location.pathname != "/message") {
      this.showNoneMessage = false;
    }
  }

  checkboxChange(event, type) {
    if (type == "inbox") {
      this.inboxFlag = event.checked;
    }
    if (type == "draft") {
      this.draftFlag = event.checked;
    }
    this.getConversations();
  }

  //
  getConversations() {
    let params = {
      inbox: this.inboxFlag ? 1 : 0,
      draft: this.draftFlag ? 1 : 0
    };
    this.Dash.GET(this.Dash.config.getMsgList, params, true).then(result => {
      this.msgList = result;
      // console.log(result);
    });
  }

  //
  getRecipients() {
    this.Dash.POST(this.Dash.config.getMsgRecipients).then(result => {
      this.recipientsList = result;
    });
  }

  //
  createDraft(type) {
    let params = {
      Sender: this.senderId,
      RecipientList: [],
      subject: "",
      text: "", 
      is_text: type
    };
    this.Dash.POST(this.Dash.config.initMsgDraft, params, true).then(result => {
      this.router.navigateByUrl(`/message/create/${type}/${result.messageid}/${result.conversationid}`);
    });
  }

  //session
  getSessionInfo() {
    let student = this.auth.userinfo.login_user;
    if (student['student'])
      this.studentId = student['student']['id'];

    if (student['viewRole'] == 'student')
      this.senderId = student['student']['id'];

    if (student['viewRole'] == 'connection')
      this.senderId = student['connection']['id'];
  }
}